* {
    padding: 0;
    margin: 0
}

body {
    margin: 0 auto;
    font-family: sans-serif;
    font-size: 16px;
    color: #333;
    min-height: 100%;
}

body,
html {
    width: 100%;
    height: 100%;
}

html,
div,
ul,
li,
dl,
dt,
dd,
p,
h1,
h2,
h3,
h4,
h5,
h6,
img,
form,
embed,
label,
button,
select,
textarea {
    margin: 0;
    padding: 0;
    border: 0;
    font-weight: normal;
    color: #333;
    outline:none;
    -webkit-tap-highlight-color:rgba(0,0,0,0);
}

img {
    border: none;
    vertical-align: middle;
}

ul,
ul li {
    list-style: none;
}

.cls {
    clear: both;
    line-height: 0px!important;
    height: 0px!important;
    font-size: 0px!important;
}

select,
input {
    vertical-align: middle;
    border: 0;
    background: none;
    webkit-border-radius: 0;
    border-radius: 0;
    outline: none;
    font-size: 16px;
    color:#333;
    /*-webkit-appearance: none;*/
}

input[disabled]{
    opacity:1
}

a {
    color: #666;
    text-decoration: none;
    display: block;
}

button{
	border: none;
	outline: none;
}

hr{
	border: 0;
}

.hidden {
    display: none;
}

.clearfix:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}

.table {
    display: table;
}

.table-cell {
    display: table-cell;
    vertical-align: middle;
}
