@import '../common.css';
@import '../style.css';
@import '../weui.css';
@import 'update-weui.less';
@import 'popup.less';

//颜色变量
@color-Blue: #2c66ce;
@color-Dark: #303847;
@color-Grey: #969eb2;
@color-LightGrey: #f1f1f1;
@color-Red: #ff5722;
@color-BGcolor: #f0f4f6;
@color-LineColor: #e2e6e8;
@color-white: #ffffff;
@color-gray: #eff0f0;
@color-black : #303232;

//字体变量
@font-22: 22px;
@font-19: 19px;
@font-16: 16px;
@font-14: 14px;
@font-12: 12px;

@space-between:15px;


@media (-webkit-min-device-pixel-ratio: 2){
	.border-top::after {
		content: '';
		width: 100%;
		position: absolute;
		top: 0;
		right: 0;
		transform: scaleY(0.5);
		-webkit-transform: scaleY(0.5);
	}
	.border-bottom::before {
		content: '';
		width: 100%;
		position: absolute;
		bottom: 0;
		right: 0;
		transform: scaleY(0.5);
		-webkit-transform: scaleY(0.5);
	}
}
.border-top::after {
	content: '';
	width: 100%;
	position: absolute;
	top: 0;
	right: 0;
	transform: scaleY(0.5);
	-webkit-transform: scaleY(0.5);
}
.border-bottom::before {
	content: '';
	width: 100%;
	position: absolute;
	bottom: 0;
	right: 0;
	transform: scaleY(0.5);
	-webkit-transform: scaleY(0.5);
}

.f-font12{
	font-size: @font-12;
}
.f-font14{
	font-size: @font-14;
}
.color-9{
	color: #999;
}
.bg-white{
	background-color: @color-white;
}
.bg-gray{
	background-color: @color-gray;
}
.form{
	display: flex;
	align-items: center;
	span{
		padding-right: 10px;
	}
	label{
		padding:20px 0;
		flex-grow: 1;
	}
	input[type="text"]{
		width: 100%;
	}
	em{
		width: 18px;
		height: 18px;
		background: url('../img/ico_arrow.png') no-repeat 0 0 /cover;
	}
}
.fixed-button-area{
	height: 80px;
}

.tag-color-1{
	color:#2c66ce
}
.tag-color-2{
	color:#289da1
}
.tag-color-3{
	color:#c88315
}
.tag-color-4{
	color:#ff5722
}
.tag-color-5{
	color:#0db417
}
.tag-color-6{
	color:#a5a8a8
}

.shade{
	width: 200px;
	position: absolute;
	top: 24px;
	left: 0;
	height: 20px;
}
.show-tag{
	display: inline-block;
	background-color: #f4f5f5;
	width: 45px;
	height: 22px;
	line-height: 22px;
	border:1px solid #e3e6e6;
	border-radius: 22px;
	font-size: 12px;
	text-align: center;
}

.small-button{
	background: -webkit-linear-gradient(top,#2b7ed6,#306acf);
	border-radius: 5px;
	color: #fff;
}
.small-button-2{
	border:1px solid #2c66ce;
	border-radius: 5px;
	color:#2c66ce;
}

.popup{
	background: rgba(0, 0, 0, 0.6);

}

.below-view{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 11;
}

.mapFlag{
	border-radius: 5px;
	background-color: rgba(0, 0, 0, .8);
	color: #fff;
	padding: 5px 10px;
	width: 200px;
	font-size: 14px;
}

.app-content{
	height: 100%;
	.app-content-body{
		height: 100%;
	}

	.address-info{
		padding:15px @space-between;
		.address-tips{
			display: flex;
			justify-content: space-between;
			align-items: center;
			h4{
				font-weight: bold;
			}
			em{
