		}
	}

	.express-info{
		margin-top: 10px;
		.express-tips{
			padding: 15px @space-between;
			display: flex;
			justify-content: space-between;
			align-items: center;
			h4{
				font-weight: bold;
			}
			em{
				width: 22px;
				height: 22px;
				background: url('../img/lico_scan.png') no-repeat 0 0 /cover;
			}
		}
