		}
	}

	.pay-type{
		display: flex;
		padding: 10px @space-between;
		border-top: 1px solid @color-gray;
		align-items: center;
		justify-content: space-between;
		.key{
			display: flex;
			a{
				border:1px solid @color-Blue;
				height: 40px;
				width: 88px;
				text-align: center;
				line-height: 40px;
				color: @color-Blue;
				&.cur{
