		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		a{
			width: 100%;
			text-align: center;
			background: -webkit-linear-gradient(top,#2b7ed6,#306acf);
			color: @color-white;
			height: 60px;
			line-height: 60px;
		}
	}

